<template>
    <div>
        <button 
            v-for="tab in tabs"
            :key="tab.title"
            :class="['tab-button' ,{active :currentTab === tab.title}]"
            @click="currentTab = tab.title"
        >
            {{tab.displayName}}
        </button>
        <component
            v-bind:is="currentTabComponent"
           class="tab">
        </component>
        <!-- <PaperMakeCustom/>
        <PaperMakeRandom/> -->
    </div>
</template>

<script>


import PaperMakeCustom from './PaperMakeCustom'
import PaperMakeRandom from './PaperMakeRandom'
export default {
    name:"PaperMake",
    components:{
        PaperMakeCustom,
        PaperMakeRandom,
        
    },
    data(){
        return {
            currentTab:'Random',
            tabs:[
                {title:'Random',displayName:'智能组卷'},
                {title:'Custom',displayName:'人工组卷'},
            ]
        }
    },
    computed:{
        currentTabComponent:function(){
            return 'PaperMake'+this.currentTab;
        }
    }
}
</script>

<style scoped>
    .tab-button{
        padding: 6px 10px;
        border-top-left-radius: 3px;
        border-top-right-radius: 3px;
        border: 1px solid #ccc;
        cursor: pointer;
        background: #f0f0f0;
        margin-bottom: -1px;
        margin-right: -1px;

    }
</style>