
<template>
    <div>人工组卷界面
        <div id="category">
            <p>试题选择界面</p>
            <select v-model="currentOption">
                <option disabled value="">请选择主标题</option>
                <option v-for="(option,index) in options"
                :key="index">
                    {{option.value}}
                </option>
            </select>
            <select v-model="currentSub">
                 <option disabled value="">请选择副标题</option>
                <option v-for="(optionsub,index) in options[MainId].optionsubs"
                :key="index">
                    {{optionsub.value}}
                </option>
            </select>
            <div id="selectframe">
                <div v-for="(questionItem,index) in questionItems"
                :key="index">
                    <p>{{questionItem.mainTitle}}</p>
                    <p>{{questionItem.subTitle}}</p>
                    <p>题干：{{questionItem.content}}</p>
                    <p>A:{{questionItem.options[0]}}  B:{{questionItem.options[1]}}</p>
                    <p>C:{{questionItem.options[2]}}  D:{{questionItem.options[3]}}</p>
                    <p>答案：{{answer}}</p>
                    <p><button @click="add(questionItem)">加入试卷</button></p>
                </div>
            </div>
            <div id="helpbar">
                <p>已选试题界面</p>
                <!-- 已经选择的试题在此显示 -->
                <p></p>
                <div v-for="(questionItem,index) in questionSheets"
                :key="index">
                    <p>{{questionItem.mainTitle}}</p>
                    <p>{{questionItem.subTitle}}</p>
                    <p>题干：{{questionItem.content}}</p>
                    <p>A:{{questionItem.options[0]}}  B:{{questionItem.options[1]}}</p>
                    <p>C:{{questionItem.options[2]}}  D:{{questionItem.options[3]}}</p>
                    <p>答案：{{answer}}</p>
                    <p><button @click="questionDelete(questionItem)">删去试题</button></p>
                </div>

            </div>
            <p><button @click="handleSubmit()">提交并下载</button></p>
        </div>
    </div>
</template>
<script>

import axios from 'axios'
import VueAxios from 'vue-axios'
import {mapGetters,mapState,mapMutations} from 'vuex'
//import questionItemVue from './questionItem.vue'
export default {
    /*先确定知识点，然后输入一个满分值*/

    name:"PaperMakeCustom",
    computed:{
          ...mapState('papermakecustomdata',{
            questionItems:"questionItems",
            questionSheets:"questionSheets",
        }),
        MainId:function(){
            let obj = this.options.findIndex(item =>item.value === this.currentOption );
            return obj;
        }
    },
    data(){
        return{
            currentOption:'概述',
            currentSub:'基本概念',
            options: [
            {   
                id: 1, 
                value: '概述', 
                optionsubs: [
                    { id: 1, value: '基本概念', },
                    { id: 2, value: '交换原理',  },
                    { id: 3, value: '网络应用需求',  },
                    { id: 4, value: '网络体系架构',  },
                    { id: 5, value: '网络性能评估',},
                ],
            },
            { 
                id: 2, 
                value: '直连网络', 
                optionsubs: [
                    { id: 1, value: '物理层基本通信', },
                    { id: 2, value: '数据链路层控制',  },
                    { id: 3, value: '可靠传输机制',  },
                    { id: 4, value: '多路访问机制',  },
                ],
            
            },
            {   id: 3, 
                value: '网络互联', 
                optionsubs: [
                    { id: 1, value: '网桥与交换机', },
                    { id: 2, value: '网络互联与路由器', },
                    { id: 3, value: 'IP服务模型与协议', },
                    { id: 4, value: '路由算法与协议',  },
                    { id: 5, value: 'IP地址及其划分', },
                    { id: 6, value: '网络层与其他协议', },
                ],

            },
            {   id: 4, 
                value: '传输层与资源分配', 
                optionsubs: [
                    { id: 1, value: 'UDP协议', },
                    { id: 2, value: 'TCP协议',  },
                    { id: 3, value: '拥塞控制', },
                ],
            },
            {   id: 5, 
                value: '应用层', 
                optionsubs: [
                    { id: 1, value: '应用层'},
                ]
            },
        ],
            Trans:[
                {
                    mainTitle:'',
                    subTitle:'',

                },
            ]

        }
    },
    
    methods:{
        ...mapMutations('papermakecustomdata',[
            'add',
            'questionDelete',
           'set',
           'reset',
           'handleSubmit',
        ]),
       
    },
    //组件创建时就申请一次试题
    created(){
        this.axios.post('/#',{
                mainTitle:this.currentOption,
                subTitle:this.currentSub,
            })
            .then(function(response){
            //这个地方要加强测试，很可能无法操作
                this.set(response.data.questionItems);
            })
            .catch(function(error){

            })
    },
    //每当标题改变时重新申请试题
    watch:{
        currentSub:function(val){
            this.axios.post('/#',{
                mainTitle:this.currentOption,
                subTitle:val,
            })
            .then(function(response){
            //这个地方要加强测试，很可能无法操作
                this.set(response.data.questionItems);
            })
            .catch(function(error){

            })
        }
    }
   
}
</script>